'use strict';

/**
* Compute the hyperbolic arccosine of a number.
*
* @module @stdlib/math-base-special-fast-acosh
*
* @example
* var acosh = require( '@stdlib/math-base-special-fast-acosh' );
*
* var v = acosh( 1.0 );
* // returns 0.0
*
* v = acosh( 2.0 );
* // returns ~1.317
*
* v = acosh( NaN );
* // returns NaN
*/

// MODULES //

var acosh = require( './acosh.js' );


// EXPORTS //

module.exports = acosh;
